.login-container {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(to right,  #131313  50%, #0b0b0b  50%);
  position: absolute;
}

.planetLogin-image {
  position: absolute;
  top: 0; 
  left: 0; 
  width: auto; 
  height: 100vh; 
  object-fit: cover; 
}

.nombre-image {
  position: absolute;
  top: 2%;
  left: 2%;
  height: 10%;
}

.contenedor {
  padding: 40px;
  border-radius: 5px;
  background-color: rgba(218, 220, 221, 0);
  text-align: left;
  margin-top: 20px;
  margin-left: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-group {
  margin-bottom:2rem;
  width: 100%;
  max-width: 300px; 
  min-height: 5rem;  
  color: #4e4e4e;
}

label {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 1.2rem;
  color: #b6b6b6;
}

.hola {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 5rem;
  padding-bottom: 18%;
  color: #aaaaaa;
  text-shadow: 4px 1px 1px  #f6f7f3; 
  text-align: center;  
}

.login-input {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  height: 1.5rem;
  padding: 0.5rem;
  font-size: 1rem;
  border-radius: 5px;
  width: 100%;
  background-color: #04b8b870; 
  color: #e7e6e6;
  border: 1px solid #11b6b600;
  margin-top: 2%;
}

.error-message {
  color: #ff0000;
  font-size: 0.8rem;
  margin-top: 0.2rem;
  height: 1.5rem; 
  padding: 0.2rem; 
  visibility: hidden; 
  overflow: hidden; 
  transition: visibility 0.5s, height 1s; 
  position: fixed;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.error-message.active {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  visibility: visible; 
  height: 1.5rem;
  
}

.login-button {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  color: rgb(61, 60, 60);
  background-color: #f8f5f5;
  border-radius: 5px;
  cursor: pointer;
  border: 2px solid #2d2d2e00;
  margin-top: 1rem; 
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; 
}

.login-button:hover {
  background-color: rgb(248, 73, 4);
  color: rgb(255, 254, 255);
  border: 2px solid #2d2e2d00;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.Saltar-button {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  position: absolute;
  color: rgb(61, 60, 60);
  background-color: #f8f5f5;
  cursor: pointer;
  border-radius: 50%; 
  width: 20vw; 
  height: 20vw; 
  max-width: 90px; 
  max-height: 90px; 
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem; 
  text-align: center;
  text-decoration: none;
  border: none;
  outline: none;
  right: 3%;
  bottom: 5%;
}

.Saltar-button:hover {
  position: absolute;  
  cursor: pointer;
  border-radius: 50%; 
  width: 20vw; 
  height: 20vw; 
  max-width: 90px; 
  max-height: 90px; 
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(248, 73, 4);
  color: rgb(255, 254, 255);
  font-size: 1rem; 
  text-align: center;
  text-decoration: none;
  border: none;
  outline: none;
  right: 3%;
  bottom: 5%;
}

@media (max-width: 768px) {
  .login-container {
    align-items: flex-start;
  }

  .planetLogin-image {
    height: 20vh;
    width: 100%;
  }

  .contenedor {
    margin-left: 2%;
  }

  .nombre-image{
    height: 13vh;
    margin-top: 0;
  }
}